import React, { useState, useCallback, useEffect } from 'react';
import { StaticContext } from 'react-router';
import { Link, RouteComponentProps } from 'react-router-dom';
import styles from './css/community.module.css';
import { GET } from '../utils/requests';
import { ProfileData } from '../interfaces';
import { FaCrown, FaDotCircle, FaCircle } from 'react-icons/fa';

export default function Community(props: RouteComponentProps<any, StaticContext, any>) {

	const [users, setUsers] = useState([]);
	const updateUsers = useCallback(() => GET({ url: '/users' })
		.then(setUsers), [setUsers]);
	useEffect(() => {
		updateUsers();
	}, [updateUsers]);

	return (
        <div id='main' className={'animated-' + props.location.state?.direction}>
			<div className={styles.container}>
				<section className='container'>
					<h1><b>Users</b></h1>
					<div className={styles.userTable}>
						{users.map((u: ProfileData) => {
							return <>
								<div className={styles.status}>{u.isLicensed ? <FaCrown /> : <FaCircle style={{fontSize: '0.5em'}} />}</div>
								<Link className={styles.profile} to={'/@/' + u.username}>{u.username}</Link>
							</>
						})}
					</div>
				</section>
				<section className='container'>
					<h1><b>Teams</b></h1>
					<div className={styles.userTable}>
						<Link className={styles.profile} to='/me/teams'>
							My teams
						</Link>
					</div>
				</section>
			</div>
		</div>
	)
}